import "./styles.css";
import React, { Component, useState } from "react";
import { render } from "react-dom";
import Keyboard from "react-simple-keyboard";
import "react-simple-keyboard/build/css/index.css";

export default function App() {
  let currentRow = 0;
  const [rowOneLetters, setRowOneLetters] = useState([]);
  const onKeyPress = (button) => {
    console.log("Button pressed", button);
    setRowOneLetters(rowOneLetters.push(button));
  };
  return (
    <div className="App">
      <Row letters={rowOneLetters} />
      {/* <Row />
      <Row />
      <Row />
      <Row />
      <Row /> */}
      <Keyboard onKeyPress={this.onKeyPress} />
    </div>
  );
}

function Square(props) {
  return (
    <div class="square">
      <p class="text">{props.text}</p>
    </div>
  );
}

function Row(props) {
  console.log(props.letters);
  return (
    <div class="rows">
      <Square text={props.letters[0]} />
      <Square text={props.letters[1]} />
      <Square text={props.letters[2]} />
      <Square text={props.letters[3]} />
      <Square text={props.letters[4]} />
    </div>
  );
}

function ShowKeyboard() {
  return <div class="keyboard"></div>;
}
